import { useEffect, useState } from 'react'
import MoreVertIcon from '@material-ui/icons/MoreVert';
import CloseIcon from '@material-ui/icons/Close';
import axios from 'axios';

export const Projects = () => {
  const [pinned, setPinned] = useState([]);
  const [status, setStatus] = useState('LOADING');

  
  useEffect(() => {
    let query = `query {
      repositoryOwner(login: "Johnnyk737") {
        ... on User {
          pinnedItems(first:6, types: REPOSITORY) {
            nodes {
              ... on Repository {
                name
                url
                description
                languages(first: 3) {
                  nodes {
                    ... on Language {
                      name
                    }
                  }
                }
              }
            }
          }
        }
      }
    }`

    axios({
      url: "https://api.github.com/graphql", 
      method: 'POST',
      data: {
        query: query
      },
      headers: {
        'Authorization': "Bearer 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",
        'Content-Type': 'application/json',
        'Accept': '*/*',
      },
    })
    .then(response => response.data)
    .then(data => {
      setPinned(data.data.repositoryOwner.pinnedItems.nodes)
      setStatus('DONE')
    })
    .catch(error => {
      console.log(error)
    })
    
  }, [])

  const showProjects = () => {
    if (status == 'DONE') {
      return pinned.map((proj, idx) => {
        return (
          <div key={idx} className="col s12 m6 l6">
            <div className="card medium">
              <div className="card-image">
                <img src="img/arcade-game.png" alt={proj.name} />
              </div>
              <div className="card-content activator">
                <MoreVertIcon />
                <h4 className="header-color"><a href={proj.url}>{proj.name}</a></h4>
                <p>{proj.description}</p>
              </div>
              <div className="card-reveal">
                <span className="card-title grey-text text-darken-4">
                  <h4 className="header-color"><a href={proj.url}>{proj.name}</a></h4>
                  <CloseIcon />
                </span>
                <p>Technologies used: 
                  {proj.languages.nodes.map(lang=>lang.name).join(", ")}
                </p>
              </div>
            </div>
          </div>
        )
      })
    } else {
      return (<div>Loading... </div>);
    }
  }

  return (
    <section id="sect-projects" className="section scroll-spy">
          <h3 className="section-color">Projects</h3>
          <div className="section-text">
            <div className="project-list">
              <div className="row">
                {showProjects()}
              </div>
            </div>
          </div>
        </section>
    )
}
